import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { changeItemFactory as cif } from '@utils';
import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';
import { EmployeeDeduction } from '@app/model';
import { ChangeSet } from 'ngrx-data';


const ENTITY_EMPLOYEE_DEDUCTION = 'EmployeeDeduction';

@Injectable({providedIn: 'root'})
export class EmployeeDeductionService extends CurrentCompanyEntityCollectionService<EmployeeDeduction> {
  constructor(protected helper: CompanyEntityHelperService) {
    super('EmployeeDeduction', helper);
  }
  /**
   * Employee Deductions
   */
  readonly employeeDeductions$ = this.entities$;

  getEmployeeDeductions(empId: string): Observable<EmployeeDeduction[]> {
    return this.employeeDeductions$.pipe(
      map(ds =>
        ds.filter(d => d.employeeId === empId)
      )
    );
  }

  getDeductionsLinkedTo(companyDeductionId: string): Observable<EmployeeDeduction[]> {
    return this.employeeDeductions$.pipe(
      map(ds =>
        ds.filter(d => d.companyDeductionId === companyDeductionId)
      )
    );
  }

   // TODO: Test with real services
  /**
   * Multi-entity save of EmployeeDeduction with adds, updates and deletes
   * @param deductions Array of deductions to add, update, or delete.
   * Deletes have isRemoved: true
   * Updates are non-deletes with an id
   * Adds are non-deletes without an id; will generate that id here.
   * @returns Terminating observable of ChangeSet that emits when server responds
   */
  save(deductions: EmployeeDeduction[]): Observable<ChangeSet> {
    const deletes = (deductions || []).filter(d => d.isRemoved === true && d.id);
    // Ignore deductions with isRemoved:true but no id because they don't exist on the server

    // Cannot use ChangeSetOp.Upsert because server does not support it.
    // Separate non-deletes into updates (have id) and adds (no id)
    const updates = (deductions || []).filter(d => d.isRemoved !== true && d.id);

    // Identity new deductions by missing id and generate their ids before adding
    const adds = (deductions || [])
    .filter(d => d.isRemoved !== true && !d.id)
    .map(d => {
      d.id = this.helper.generateId();
      return d;
    });

    const changes = [
      cif.add(ENTITY_EMPLOYEE_DEDUCTION, adds),
      cif.update(ENTITY_EMPLOYEE_DEDUCTION, updates),
      cif.delete(ENTITY_EMPLOYEE_DEDUCTION, deletes)
    ];
    const changeSet: ChangeSet<EmployeeDeduction> = { changes };

    return this.saveEntities(changeSet, '/save/employee-deduction');
  }
}
