import { CompanyEntityBase } from './company-entity-base';
import { UserRole } from '@model/codes-enums';

/** User with access to the application. */
export class User extends CompanyEntityBase {
  /** Id of the user in this application */
  id: string;
  /**
   * Id of the only company this user can see.
   * Required unless userRole is Administrator | Agent | Sales,
   * which are users of multiple companies
   * Only the Administrator role is implemented now.
   */
  companyId: string = null;
  /** Unique OIDC id of the user in SP IdentityServer */
  oidcSub: string;
  username: string;
  userRole: UserRole;
  firstName: string;
  lastName: string;
  email: string;
  phone: string;
}
