import { Injectable } from '@angular/core';
import { EntityActionOptions } from 'ngrx-data';
import { map } from 'rxjs/operators';
import { Address } from '@model';
import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';

/**
 * Aggregates services for dealing with addresses.
 * Available in and collaborates with AppEntityServices
 */
@Injectable({providedIn: 'root'})
export class AddressService extends CurrentCompanyEntityCollectionService<Address> {
  constructor(protected helper: CompanyEntityHelperService) {
    super('Address', helper);
  }

  add(address: Address, options?: EntityActionOptions) {
    this.blockBadAddress(address);
    return super.add(Object.assign({}, address), options);
  }

  blockBadAddress(address: Address) {
    if (address == null) {
      throw new Error('Address to save is undefined');
    }
    if (address.isRemoved && (address.isHome || address.isMain)) {
      throw new Error('Cannot remove (delete) the main Company address or a home address');
    }
  }

  update(address: Partial<Address>, options?: EntityActionOptions) {
    this.blockBadAddress(address as Address);
    return super.update(Object.assign({}, address), options);
  }

  upsert(address: Address, options?: EntityActionOptions) {
    this.blockBadAddress(address as Address);
    return super.upsert(Object.assign({}, address), options);
  }

  /**
   * Observable of the Company live work addresses (cloned as Address class instances and sorted)
   */
  readonly workAddresses$ = this.entities$.pipe(
    map(addresses => {
      return addresses
        .filter(addr => !addr.isRemoved && !addr.isHome)
        .map(addr => Address.create(addr))
        .sort((l, r) => l.toString().localeCompare(r.toString()));
    })
  );
}
