import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EntityDispatcher } from 'ngrx-data';

import { of, Observable } from 'rxjs';
import { catchError, map, shareReplay, switchMap, withLatestFrom } from 'rxjs/operators';

import { Deduction } from '@model';
import { EntityHelperService } from './entity-helper.service';
import { environment } from '@environment';
import { LoggerService } from '@core';

const deductionsUrl = environment.apiRoot + '/deductions';

@Injectable({providedIn: 'root'})
export class DeductionService {

  /**
   * Non-terminating observable of ECommerce Deduction master metadata
   * Return cached deductions if already loaded, else loads from server.
   * If server load fails, returns previous deductions (if any)
   * @returns Cold, non-terminating observable of deductions. Must subscribe and unsubscribe.
   */
  deductions$: Observable<Deduction[]>;

  private dispatcher: EntityDispatcher<Deduction>;

  /** Non-terminating, boolean observable indicating if Deductions have been loaded successfully */
  loaded$: Observable<boolean>;

  constructor(
    entityHelper: EntityHelperService,
    private http: HttpClient,
    private logger: LoggerService
  ) {
    const elements = entityHelper.collectionServiceElementsFactory.create<Deduction>('Deduction');
    this.dispatcher = elements.dispatcher;
    this.loaded$ = elements.selectors$.loaded$;

    this.deductions$ = elements.selectors$.entities$.pipe(
      withLatestFrom(this.loaded$),
      switchMap(([deductions, loaded]) => {
        return loaded
          ? of(deductions)
          : this.http.get<Deduction[]>(deductionsUrl).pipe(
            map(newDeductions => {
              this.dispatcher.addAllToCache(newDeductions);
              return newDeductions;
            }),
            catchError(error => {
               // Swallow error and return previous deductions (if any)
               this.logger.error('Deductions service failed to return deductions', error);
               this.dispatcher.setLoaded(true); // pretend it loaded successfully
               return of(deductions);
            })
          );
      }),
      shareReplay(1)
    );
  }

  /**
   * Clear the Deductions master metadata in cache. Next `getDeductions()` will refresh it.
   */
  clearCache() {
    this.dispatcher.clearCache(); // also clears loaded$ flag
  }
}
