import { CompanyEntityBase } from './company-entity-base';
import { EnvelopeStatus } from '@app/model/codes-enums';

/** Represents a collection of documents for sending or signing, e.g. through DocuSign */
export class Envelope extends CompanyEntityBase {
  id: string;
  companyId: string;

  status: EnvelopeStatus;

  /** id of the envelope in docusign */
  eSignEnvelopeId: string;
  isWetSign: boolean;
  createdDate: Date;
  statusDate: Date;
  /** True when marked-for-delete */
  isRemoved: boolean;
}
