import { TaxBehaviorWrapper } from '@app/core/services/tax-services/tax-models';
import { EmployeeTax, CompanyTax } from '@app/model';
import { Injectable } from '@angular/core';

export enum TaxCatalogKey {
    /**
     * Note that FWhTax is the only tax that you will NOT find in the CompanyTaxes
     * Its done by design as that tax is the on the Company object itself.
     * However FWhTax IS available in EmployeeTaxes */
    FWhTax = 'Fwh',
    Futa = 'Futa',
    AnySWhTax = 'ASwh',
    AnySuta = 'ASuta',
}

interface ITaxSelector {
    select<T extends EmployeeTax | CompanyTax>(taxesWithBehaviors: TaxBehaviorWrapper<T>[]):
        TaxBehaviorWrapper<T>;
}


@Injectable({ providedIn: 'root' })
export class TaxSelectorService {
    private selectorCatalog: { [type: string]: ITaxSelector } = {};

    constructor() {
        this.selectorCatalog[TaxCatalogKey.FWhTax] = {
            select: (taxes) => taxes.find(t => t.mtsTaxCode === 'FE0000-001')
        };

        this.selectorCatalog[TaxCatalogKey.Futa] = {
            select: (taxes) => taxes.find(t => t.mtsTaxCode === 'FE0000-010')
        };

        this.selectorCatalog[TaxCatalogKey.AnySWhTax] = {
            select: (taxes) => taxes.find(t => t.mtsTaxCode.endsWith('0000-001'))
        };

        this.selectorCatalog[TaxCatalogKey.AnySuta] = {
            select: (taxes) => taxes.find(t => t.mtsTaxCode.endsWith('0000-010'))
        };
    }

    public select<T extends EmployeeTax | CompanyTax>(taxCatalogKey: TaxCatalogKey, taxes: TaxBehaviorWrapper<T>[]) {
        const selector = this.selectorCatalog[taxCatalogKey];
        if (selector == null) {
            return null;
        }

        return selector.select(taxes);
    }
}
