import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'sp-button-group',
  templateUrl: './button-group.component.html'
})
export class ButtonGroupComponent implements OnInit {
  @Input() form: FormGroup;
  @Input() controlName: string;
  @Input() options: { name: string; value: any }[] | string[];
  control: FormControl;
  values: { name: string; value: any }[];

  constructor() {}

  ngOnInit() {
    this.control = this.form.get(this.controlName) as FormControl;
    const opt = this.options;
    if (opt && opt.length) {
      if (typeof opt[0] === 'string') {
        const sopt = opt as string[];
        this.values = sopt.map(s => ({ name: s, value: s }));
      } else {
        this.values = opt as { name: string; value: any }[];
      }
    }
  }

  setValue(v: any) {
    // let val = this.values[i].value;
    this.control.setValue(v.value);
  }

  isValue(v: any): boolean {
    // let val = this.values[i].value;
    return this.control.value === v.value;
  }
}
