import { Pipe, PipeTransform } from '@angular/core';

/** Hide an item with `isRemoved` == true unless showRemoved is true. */
@Pipe({ name: 'filterRemoved', pure: true })
export class FilterRemovedPipe implements PipeTransform {
  transform(
    items: { isRemoved: boolean }[],
    showRemoved?: boolean,
    ...args: any[]
  ): any[] {
    return items
      ? showRemoved
        ? items
        : items.filter(item => !item.isRemoved)
      : [];
  }
}
