import { Injectable } from '@angular/core';
import { EntityCollectionServiceBase } from 'ngrx-data';

import { Observable, throwError } from 'rxjs';

import { CompanyEntityHelperService } from './company-entity-helper.service';
import { User, UserRole } from '@model';

@Injectable({providedIn: 'root'})
export class UserService extends EntityCollectionServiceBase<User> {
  constructor(private helper: CompanyEntityHelperService) {
    super('User', helper.collectionServiceElementsFactory);
  }

  /**
   * Observable of the id of the current user for this account
   * Not necessarily the authenticated user who could be an admin acting on the current user's behalf.
   */
  currentUserId$ = this.helper.currentUserId$;

  /**
   * Observable of the current user for this account
   * Not necessarily the authenticated user who could be an admin acting on the current user's behalf.
   */
  currentUser$ = this.helper.currentUser$;

  /** emits true when the current company is "read-only" and saves are disallowed. */
  readOnly$ = this.helper.readOnly$;

  /**
   * Add a new user.
   * NB: this user is not made the current user
   * @param newUser Data for a new user
   */
  addUser(newUser: User): Observable<User> {
    return throwError('Do not call add. Create a User through sign-up');
  }
}
