import { Component, Input } from '@angular/core';
import { WellKnownRoutes } from '@core/well-known-routes';

/** A placeholder until the view is developed. */
@Component({
  selector: 'sp-placeholder',
  template: `
  <div class="standard d-flex flex-column">
      <h2>{{title}}</h2>
      <div class="placeholder-img">
        <a [routerLink]="['/dashboard']">
          <img src="/assets/images/form-placeholder.png" width="auto" height="300">
        </a>
      </div>
      <div>
        <ng-content></ng-content>
      </div>
      <sp-nav-bottom [back]="back" [next]="next"></sp-nav-bottom>
    </div>
  `,
  styles: [
    `
      .placeholder-img {
        margin: 2rem 1rem;
      }
    `
  ]
})
export class PlaceholderComponent {
  @Input() title: string;
  @Input() showButtons = true;
  back: string = WellKnownRoutes.Dashboard;
  next: string = WellKnownRoutes.Dashboard;
}
