import { CompanyEntityBase } from './company-entity-base';
import { DeductionKeyValue } from './deduction';

/**
 * Deduction of an employee
 *  Employee has a list of EmployeeDeductions with some parametrization
 *  where deductions types are a subset of company deductions.
 */
export class EmployeeDeduction extends CompanyEntityBase {
  employeeId: string;
  companyDeductionId: string;
  deductionCodeTypeId: string;
  parameters: DeductionKeyValue[];
  isRemoved = false;

  constructor(id?: string, companyId?: string, employeeId?: string,
              deductionCodeTypeId?: string, companyDeductionId?: string) {
    super(id, companyId);
    this.employeeId = employeeId;
    this.deductionCodeTypeId = deductionCodeTypeId;
    this.companyDeductionId = companyDeductionId;
  }
}
