import { Injectable } from '@angular/core';

import { Observable, of, Subject } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { AddressLike, GeoAddressLike } from '@utils/address-utils';
import { TaxAddressService } from './tax-address.service';
import { IAddressValidation } from './tax-models';

/**
 * Gets the raw data from the Tax service.
 */
@Injectable({ providedIn: 'root' })
export class TaxAddressValidationService {
  private cache: { [key: string]: IAddressValidation } = {};

  constructor(private addressProviderService: TaxAddressService) {}

  validateAddress(address: AddressLike): Observable<IAddressValidation> {
    const key = this.getKey(address);
    const result = this.cache[key];
    if (result) {
      return of(result);
    }

    return this.addressProviderService.validateAddress(address).pipe(
      tap(addr => (this.cache[key] = addr)),
      catchError((error: any) => of({ isValid: false, suggestedAddresses: [] }))
    );
  }

  hasSuggestion(address: AddressLike): boolean {
    const key = this.getKey(address);
    const result = this.cache[key];
    return (
      result && !result.isValid && result.suggestedAddresses && result.suggestedAddresses.length > 0
    );
  }

  private getKey(address: AddressLike): string {
    // exclude street2 - not important for taxes TODO validate
    const streetAddress = {
      street1: address.street1,
      city: address.city,
      state: address.state,
      zip: address.zip
    };
    return JSON.stringify(streetAddress);
  }
}
