import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '@environment';

export interface TinNameResult {
  tinCode: number;
  tinDetails: string;
  dmfCode: number;
  dmfDetails: string;
  dmfData: string;
  einCode: number;
  einDetails: string;
  einData: string;
}

/*
 * Validate EIN
 */
@Injectable({ providedIn: 'root' })
export class EinLookupService {
  constructor(private http: HttpClient) {}

  /** Validate that the EIN belongs to the company with call to SP API*/
  validateTinName(ein: string, companyName: string): Observable<TinNameResult> {
    const url = environment.spApiRoot + '/tincheck/tinname';
    const body = {
      tin: ein,
      name: companyName
    };

    return this.http.get(url, { params: body }) as Observable<TinNameResult>;
  }
}
