import { Injectable } from '@angular/core';
import { CanActivate, CanActivateChild, Router } from '@angular/router';

import { combineLatest, Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';

import { AuthService } from '@core/services/auth-services/auth.service';
import { User, UserRole } from '@model';
import { WellKnownRoutes } from '@core';

/**
 * Guard attempts to get to the Signup Page
 */
@Injectable({ providedIn: 'root' })
export class SignupGuard implements CanActivate, CanActivateChild {

  constructor(
    private authService: AuthService,
    private router: Router
  ) {}

  canActivate(): Observable<boolean>  {
    return this.isSignedOut$;
  }

  canActivateChild(): Observable<boolean> {
    return this.isSignedOut$;
  }

  // Continue to sign up only if signed out. Must sign out before sign up.
  // If signed in as regular user, navigate to dashboard.
  // If signed in as admin, sign out (an admin cannot sign up).
  private get isSignedOut$() {
    return combineLatest(
      this.authService.isAdmin$,
      this.authService.isSignedIn$
    ).pipe(
      first(),
      map(([isAdmin, isSignedIn]) => {
        if (isSignedIn) {
          if (isAdmin) {
            this.authService.logEvent(`SignupNavigationGuard calling AuthService.signout() for signed in Administrator user.`);
            this.authService.signout();
          } else {
            // TODO: consider signing out instead.
            const newRoute = WellKnownRoutes.Dashboard;
            this.authService.logEvent(`SignupNavigationGuard re-routing signed in regular user to "${newRoute}"`);
            this.router.navigate([newRoute], { replaceUrl: true });
          }
        } else {
          const reroute = this.authService.getReroute('SignupNavigationGuard');
          if (reroute === WellKnownRoutes.SignedOut) {
            this.router.navigate([reroute], { replaceUrl: true });
          }
        }
        return !isSignedIn;
      }),
      first()
    );
  }
}
