import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ChangeSet } from 'ngrx-data';
import { changeItemFactory as cif } from '@utils';

import { CompanyDeduction, Deduction } from '@model';
import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';

const ENTITY_COMPANY_DEDUCTION = 'CompanyDeduction';

@Injectable({providedIn: 'root'})
export class CompanyDeductionService extends CurrentCompanyEntityCollectionService<CompanyDeduction> {
  constructor(protected helper: CompanyEntityHelperService) {
    super(ENTITY_COMPANY_DEDUCTION, helper);
  }
  /**
   * Company Deductions
   */
  readonly companyDeductions$ = this.entities$;

  /**
   * Create new CompanyDeduction from a deduction master.
   * The id is empty because will save with upsert.
   */
  createFromDeduction(deduction: Deduction): CompanyDeduction {
    if (!deduction) {
      throw new Error('deduction is null');
    }
    return this.create(new CompanyDeduction(deduction));
  }

  // TODO: Test with real services
  /**
   * Multi-entity save of CompanyDeduction with adds, updates and deletes
   * @param deductions Array of deductions to add, update, or delete.
   * Deletes have isRemoved: true
   * Updates are non-deletes with an id
   * Adds are non-deletes without an id; will generate that id here.
   * @returns Terminating observable of ChangeSet that emits when server responds
   */
  save(deductions: CompanyDeduction[]): Observable<ChangeSet> {
    const deletes = deductions.filter(d => d.isRemoved === true && d.id);
    // Ignore deductions with isRemoved:true but no id because they don't exist on the server

    // Cannot use ChangeSetOp.Upsert because server does not support it.
    // Separate non-deletes into updates (have id) and adds (no id)
    const updates = deductions.filter(d => d.isRemoved !== true && d.id);

    // Identity new deductions by missing id and generate their ids before adding
    const adds = deductions
    .filter(d => d.isRemoved !== true && !d.id)
    .map(d => {
      d.id = this.helper.generateId();
      return d;
    });

    const changes = [
      cif.add(ENTITY_COMPANY_DEDUCTION, adds),
      cif.update(ENTITY_COMPANY_DEDUCTION, updates),
      cif.delete(ENTITY_COMPANY_DEDUCTION, deletes)
    ];
    const changeSet: ChangeSet<CompanyDeduction> = { changes };

    return this.saveEntities(changeSet, '/save/company-deduction');
  }
}
