import { CompanyEntityBase } from './company-entity-base';

/** Document Signer or reviewer in e-signing */
export class EnvelopeSigner extends CompanyEntityBase {
  id: string;
  companyId: string;
  envelopeId: string;
  /** Optional reference to User from which this record was created; Only populated if IsEmbedded=true */
  userId?: string;

  // TODO: create enum for this
  /** Sender, Signer, Reviewer, etc */
  role: string;

  firstName: string;
  lastName: string;
  email: string;
  /** Title, from the list determined by the Company.companyType */
  title: string;
  /** True=signing in the app; False=signing via email link */
  isEmbedded: boolean;
  /** if users must esign in a particular order */
  eSignOrder: number;
  /** from esign - canceled, declined, exception, signing_complete, etc. */
  status: string;
  statusDate: Date;
  /** True when marked-for-delete */
  isRemoved: boolean;
}
