/**
 * Generate a mostly-random 6 character password.
 * Has at least one digit and uppercase letter
 * No "oO0" or "l1" because these all look alike to a user.
 */
export function genPassword(): string {
  const upper = 'ABCDEFGHIJKLMNPQRSTUVWXYZ';
  const digit = '23456789';
  const all = 'abcdefghijkmnpqrstuvwxyz' + digit + upper;

  const u = upper[Math.floor(Math.random() * upper.length)];
  const d = digit[Math.floor(Math.random() * digit.length)];

  const charIx = Array.from({length: 4}, () => Math.floor(Math.random() * all.length));
  const pwd = shuffle(charIx.map(ix => all[ix]).concat(u, d)).join('');
  return pwd;
}

/**
 * Shuffles array in place. ES6 version
 * @param {Array} a items An array containing the items.
 */
function shuffle(a: string[]) {
  for (let i = a.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [a[i], a[j]] = [a[j], a[i]];
  }
  return a;
}
