import { CompanyEntityBase } from './company-entity-base';

/** Track status of documents requiring signature */
export class Document extends CompanyEntityBase {
  id: string;
  companyId: string;
  title: string;
  /** POA, PSA, FederalForms */
  documentType: string;
  /** For POA: StateSUI, StateSIT, Generic, MBT, EFT */
  formId: string;
  /** State code if this is a document specific to a state */
  state: string;
  envelopeId: string;
  /** id of the document in docusign */
  eSignDocumentId: number;
  createdDate: Date;
  reviewedDate: Date;
  isValid: boolean;
  comment: string;
  /** True when marked-for-delete */
  isRemoved: boolean;
}
